import { App } from "vue";
import "../libs";
import { VueCookieNext } from "vue-cookie-next";
import router from "@/router";

const cookie = VueCookieNext;
type ppgt = {
  currentPage: number,
  endIndex: number,
  endPage: number,
  pageSize: number,
  pages: Array<any>,
  startIndex: number,
  startPage: number,
  totalItems: number,
  totalPages: number,
}

const globalFunctions = {
  async removeDoubleModal(idm: string) {
    const id = cash("[data-modal-replacer=" +idm.slice(1, idm.length)+ "]");
    // console.log("id cash modal ", id);

    for (let i = 0; i < id.length; i++) {
      // console.log("id cash modal loop ", i, id);
      if ( i !== 0 ) {
        id[i].remove();
      }
    }
  },
  async showModal(id: string) {
    await globalFunctions.removeDoubleModal(id);
    await cash(id).modal("show");
  },
  async hideModal(id: string) {
    await globalFunctions.removeDoubleModal(id);
    await cash(id).modal("hide");
  },
  async toggleModal(id: string) {
    await globalFunctions.removeDoubleModal(id);
    await cash(id).modal("toggle");
  },
  upperCase(txt: string): string {
    return txt.toUpperCase();
  },
  lowerCase(txt: string): string {
    return txt.toLowerCase();
  },
  trimText(txt: any): any {
    if (txt === undefined || txt === "" || txt === null) {
      return "";
    } else {
      return txt;
    }
  },
  gLoading: {
    async show() {
      // cash("#gLoading").modal("show");
      await globalFunctions.removeDoubleModal("#gLoading");
      globalFunctions.showModal("#gLoading");
    },
    async hide() {
      // cash("#gLoading").modal("hide");
      await globalFunctions.removeDoubleModal("#gLoading");
      globalFunctions.hideModal("#gLoading");
    },
    async toggle() {
      // cash("#gLoading").modal("toggle");
      await globalFunctions.removeDoubleModal("#gLoading");
      globalFunctions.toggleModal("#gLoading");
    },
  },
  logout(): void {
    globalFunctions.gLoading.show();
    cookie.removeCookie("login-data");
    cookie.removeCookie("list-menu");
    cookie.removeCookie("tkn-data");
    cookie.removeCookie("role-data");
    cookie.removeCookie("rdm-nbr");
    setTimeout(() => {
      globalFunctions.gLoading.hide();
      router.push({name: "login"});
    }, 1000);
  },
  getLoginData(): any {
    const ld = cookie.getCookie("login-data");
    return ld;
  },
  getRole(): any {
    const role = cookie.getCookie("role-data");
    return role;
  },
  getToken(): any {
    const token = cookie.getCookie("tkn-data");
    return token;
  },
  getRdmNbr(): any {
    const rdmNbr = cookie.getCookie("rdm-nbr");
    return rdmNbr;
  },
  configGet(): any {
    const token =  globalFunctions.getToken();
    const obj = {
      headers: {
        Authorization: token,
      },
    };
    if ( token === null ) {
      globalFunctions.somethingWrong();
    } else {
      return obj;
    }
  },
  configXwfu(): any {
    const token =  globalFunctions.getToken();
    const obj = {
      headers: {
        Authorization: token,
        "Content-Type": "application/x-www-form-urlencoded",
      },
    };
    if ( token === null ) {
      globalFunctions.somethingWrong();
    } else {
      return obj;
    }
  },
  getParameterByName(name: string): any {
    const url = window.location.href;
    name = name.replace(/[\\[\]]/g, '\\$&');
    const regex = new RegExp('[?&]' + name + '(=([^&#]*)|&|#|$)');
    const results = regex.exec(url);
    if (!results) return null;
    if (!results[2]) return '';
    return decodeURIComponent(results[2].replace(/\+/g, ' '));
  },
  getParameterByNameWL(urlLink: string, name: string): any {
    const url = urlLink;
    name = name.replace(/[\\[\]]/g, '\\$&');
    const regex = new RegExp('[?&]' + name + '(=([^&#]*)|&|#|$)');
    const results = regex.exec(url);
    if (!results) return null;
    if (!results[2]) return '';
    return decodeURIComponent(results[2].replace(/\+/g, ' '));
  },
  async handleJwtExpired(error: any) {
    const rs = error.response;
    const jwt = rs.data.diagnostic.message;
    if ( jwt === "jwt expired" ) {
      globalFunctions.showModal('#modal-jwt-expired');
    }
  },
  async handleEds(error: any) {
    const ers = error.response;
    const eds = ers.data.diagnostic;
    await globalFunctions.msgHandler(false, 'Error!', eds.message);
  },
  somethingWrong() {
    globalFunctions.showModal('#modal-something-wrong');
  },
  paginate(
    totalItems: number,
    currentPage: number,
    pageSize: number,
    maxPages: number,
  ) : ppgt {
    // calculate t otal pages
    const totalPages = Math.ceil(totalItems / pageSize);

    // ensure current page isn't out of range
    if (currentPage < 1) {
      currentPage = 1;
    } else if (currentPage > totalPages) {
      currentPage = totalPages;
    }

    let startPage: number;
    let endPage: number;
    if (totalPages <= maxPages) {
      // total pages less than max so show all pages
      startPage = 1;
      endPage = totalPages;
    } else {
      // total pages more than max so calculate start and end pages
      const maxPagesBeforeCurrentPage = Math.floor(maxPages / 2);
      const maxPagesAfterCurrentPage = Math.ceil(maxPages / 2) - 1;
      if (currentPage <= maxPagesBeforeCurrentPage) {
        // current page near the start
        startPage = 1;
        endPage = maxPages;
      } else if (currentPage + maxPagesAfterCurrentPage >= totalPages) {
        // current page near the end
        startPage = totalPages - maxPages + 1;
        endPage = totalPages;
      } else {
        // current page somewhere in the middle
        startPage = currentPage - maxPagesBeforeCurrentPage;
        endPage = currentPage + maxPagesAfterCurrentPage;
      }
    }

    // calculate start and end item indexes
    const startIndex = (currentPage - 1) * pageSize;
    const endIndex = Math.min(startIndex + pageSize - 1, totalItems - 1);

    // create an array of pages to ng-repeat in the pager control
    const pages = Array.from(Array((endPage + 1) - startPage).keys()).map(i => startPage + i);

    // return object with all pager properties required by the view
    return {
      totalItems: totalItems,
      currentPage: currentPage,
      pageSize: pageSize,
      totalPages: totalPages,
      startPage: startPage,
      endPage: endPage,
      startIndex: startIndex,
      endIndex: endIndex,
      pages: pages,
    };
  },
  setUpDataCat(data: any, kode: string):void {
    window.localStorage.setItem('dataCat'+ kode, JSON.stringify(data));
  },
  setUpPayloadSubmitHasilCat(data: Array<any>, kode: string): void {
    // console.log('data set up ', data);
    const hasil = data.map((e) => {
      const obj = {
        nomor: e.nomor,
        idSoal: e.id,
        opsi: e.answer,
        ragu: e.ragu ? "Y" : "N",
      };
      // console.log('e.ragu ? "Y" : "N" ', e.ragu ? "Y" : "N");
      return obj;
    });
    window.localStorage.setItem('hasilCat'+ kode, JSON.stringify({hasil: hasil}));
    window.localStorage.setItem('soalCat'+ kode, JSON.stringify(data));
  },
  checkSetUpPayloadSHC(kode: string):boolean {
    const hasilCat = window.localStorage.getItem('hasilCat'+ kode);
    const soalCat = window.localStorage.getItem('soalCat'+ kode);

    if ( hasilCat === null || soalCat === null ) {
      return false;
    } else {
      return true;
    }
  },
  getDataCat(kode: string): any {
    const dataCat = window.localStorage.getItem('dataCat'+ kode);
    const rs = JSON.stringify(dataCat);
    const rss = JSON.parse(rs);
    return rss;
  },
  memberHasilCat(kode: string) {
    const hasilCat = window.localStorage.getItem('hasilCat'+ kode);
    const rs = JSON.stringify(hasilCat);
    return JSON.parse(rs);
  },
  getSoalCat(kode: string): any {
    const soalCat = window.localStorage.getItem('soalCat'+ kode);
    const rs = JSON.stringify(soalCat);
    const rss = JSON.parse(rs);
    return rss;
  },
  randomNumbers(from: number, to: number, length: number): string {
    const numbers = [0];
    for (let i = 1; i < length; i++) {
      numbers.push(Math.ceil(Math.random() * (from - to) + to));
    }

    return numbers.join("-");
  },
  async msgHandler(type: boolean, tmsg: string, cmsg: string) {
    const id = "#modal-msg-handler";
    type ? cash(id + ' .ic-s').show() : cash(id + ' .ic-e').show();
    const tmsgEl = cash(id + ' .t-msg');
    const cmsgEl = cash(id + ' .c-msg');

    tmsgEl[0].innerHTML = tmsg;
    cmsgEl[0].innerHTML = cmsg;
    console.log('tmsg ', (id + '.t-msg'), tmsgEl);

    await globalFunctions.showModal(id);
  }
};

const install = (app: App): void => {
  app.config.globalProperties.$gf = globalFunctions;
};

export { install as default, globalFunctions as gf };
