
import { defineComponent } from "vue";
import { gf as $gf } from "@/utils/global-functions";

export default defineComponent({
  props: {
    icon: {
      type: String,
      default: "",
    },
    color: {
      type: String,
      default: "#FFFFFF",
    },
  },
  methods: {
    logout() {
      $gf.logout();
    },
    hideGLoading() {
      $gf.gLoading.hide();
    }
  }
});
